import React from 'react'
import PropTypes from 'prop-types';


const Looper = ({value}) => {
    const jsx = value.map(val => {
        return <li className="ls_li" key={`${val}`}>{val}</li>
    });
  return (
    <div>
      {jsx}
    </div>
  )
}
Looper.propTypes = {
    value: PropTypes.array.isRequired
}
export default Looper;