import React, { Component } from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import PropTypes from 'prop-types';
// // import axios from 'axios';
import {setToken} from '../actions/authAction';
// import * as firebase from 'firebase/app';
// import 'firebase/messaging';
// import {server} from '../config/keys';

import {messaging} from  '../App';
class Starter extends Component {
  constructor() {
    super();
    this.state = {
      subscribed: false
    }
    this.showPermission = this.showPermission.bind(this);
  }
  componentWillMount() {
    this.setState({user: this.props.user});
    if(this.props.user.subscribed) {
      this.props.history.push('/events')
    }
  }
  showPermission(e) {
    // const messaging = firebase.messaging();
    messaging.usePublicVapidKey("BMRMW2uN6M8ZR7WdFHl-N-3u7IU-X2t-ZvgFuDiMC6DrpD2eaL0TQyg6hGiTg9TWuMVGBViFcahKuISZ2ZjHRUU");
    messaging.requestPermission()
      .then(() => {
        this.setState({subscribed: true})
        return messaging.getToken()
          .then(token => {
            if(token) {
              this.props.setToken(token);
              localStorage.setItem('token',JSON.stringify(token));
              this.props.history.push('/events');
            }
          })
          .catch(err => console.log(err));
      })
      .catch(err => {
        console.log('your wont get any notification');
      });
  }
  nothing(e) {
    console.log('done..')
  }
  render() {
    const value = this.state.subscribed === true ? this.nothing : this.showPermission;
    return (
      <div>
        <div className="starter_main_content">
            <h1 className="starter_main_head">Welcome to our site?</h1>
            <h3 className="starter_main_p">Subscribe to get notification</h3>
            <button className="starter_subscribe_btn btn" onClick={value}>Subscribe</button>
            <Link to="/eventDetail" exact="true">Link here</Link>
        </div>
      </div>
    )
  }
}
Starter.propTypes = {
  setToken: PropTypes.func.isRequired
}
const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = {
  setToken,
}
export default connect( mapStateToProps, mapDispatchToProps)(Starter);
