import React, { Component } from 'react';
import axios from 'axios';

class Landing extends Component {
    constructor() {
        super();
        this.state = {
            value: ''
        }
    }
    componentWillMount() {
        console.log('entered');
        axios.get('/super')
            .then(data => this.setState({value: data.data.value}))
            .catch(err => console.log(err));
    }
    render() {
        return (
            <div>  
                <h1>landing</h1>      
                <h1>{this.state.value}</h1>
            </div>
        )
    }
}
export default Landing;